'use strict'

import { TILES_PER_LINE, TILES_PER_COLUMN, TILE_WIDTH, TILE_HEIGHT, MAP_TYPE_WORLD, MAP_TYPE_CAVERNS, MAP_TYPE_LEVEL1 } from '../constant'
import { Map } from '../model/map'
import { Tile } from '../model/tile'
import resource from '../resource'
import HitboxManager from './hitbox-manager'
import { TileTransition } from '../model/tile-transition'
import { Extra } from '../model/extra'
import { Character } from '../model/character'

export default {
  world: [],
  caverns: [],
  level1: [],
  level2: [],
  level3: [],
  level4: [],
  level5: [],
  level6: [],
  level7: [],
  level8: [],
  level9: [],

  createMaps (data) {
    this.world = this.createMapsForType('world', data.world)
    this.caverns = this.createMapsForType('caverns', data.caverns)
    this.level1 = this.createMapsForType('level1', data.level1)
  },

  createMapsForType (type, mapsData) {
    const maps = []
    for (let i = 0; i < mapsData.length; i++) {
      const mapData = mapsData[i]
      const column = mapData.x
      const line = mapData.y
      const width = TILES_PER_LINE * TILE_WIDTH
      const height = TILES_PER_COLUMN * TILE_HEIGHT
      const x = column * width
      const y = line * height
      const tiles = this.createMapTiles(mapData.tiles, x, y)
      if (!maps[column]) {
        maps[column] = []
      }
      const characters = []
      for (const characteraData of mapData.characters) {
        const characterColumn = characteraData.x
        const characterLine = characteraData.y
        const characterWidth = TILE_WIDTH
        const characterHeight = TILE_HEIGHT
        const characterX = x + characterColumn * characterWidth
        const characterY = y + characterLine * characterHeight
        const characterSprite = resource.getSprite(characteraData.sprite)
        const characterText = characteraData.text
        characters.push(new Character(characterColumn, characterLine, characterX, characterY, characterWidth, characterHeight, characterSprite, characterText))
      }
      const extras = []
      for (const extraData of mapData.extras) {
        const extraColumn = extraData.x
        const extraLine = extraData.y
        const extraWidth = TILE_WIDTH
        const extraHeight = TILE_HEIGHT
        const extraX = x + extraColumn * extraWidth
        const extraY = y + extraLine * extraHeight
        const extraSprite = resource.getSprite(extraData.sprite)
        extras.push(new Extra(extraColumn, extraLine, extraX, extraY, extraWidth, extraHeight, extraSprite))
      }
      maps[column][line] = new Map(column, line, x, y, width, height, type, tiles, characters, extras)
    }

    return maps
  },

  createMapTiles (tilesData, offsetX, offsetY) {
    const tiles = []
    for (let i = 0; i < tilesData.length; i++) {
      const tileData = tilesData[i]
      const column = tileData.x
      const line = tileData.y
      const width = TILE_WIDTH
      const height = TILE_HEIGHT
      const x = column * width + offsetX
      const y = line * height + offsetY
      const sprite = resource.getSprite(tileData.sprite)
      const block = HitboxManager.getHitboxFromString(x, y, width, height, tileData.hitbox)
      const transition = new TileTransition(tileData.transition?.start, tileData.transition?.targetMapType, tileData.transition?.targetMapColumn, tileData.transition?.targetMapLine, tileData.transition?.targetTileColumn, tileData.transition?.targetTileLine, tileData.transition?.end)
      if (!tiles[column]) {
        tiles[column] = []
      }
      tiles[column][line] = new Tile(column, line, x, y, width, height, tileData.hitbox, sprite, block, undefined, transition, !!tileData.start)
    }

    return tiles
  },

  getMap (type, column, line) {
    return this[type] && this[type][column] && this[type][column][line] ? this[type][column][line] : undefined
  },

  findStartingPosition () {
    for (const map of this.mapsIterator(MAP_TYPE_WORLD)) {
      for (const tile of map.tilesIterator()) {
        if (tile.start) {
          return {
            map,
            tileColumn: tile.column,
            tileLine: tile.line
          }
        }
      }
    }
  },

  * mapsIterator (type) {
    for (let column = 0; column < this[type].length; column++) {
      for (let line = 0; line < this[type][column].length; line++) {
        yield this[type][column][line]
      }
    }
  },

  exportGame () {
    const world = this.exportMaps(MAP_TYPE_WORLD)
    const caverns = this.exportMaps(MAP_TYPE_CAVERNS)
    const level1 = this.exportMaps(MAP_TYPE_LEVEL1)

    return { world, caverns, level1 }
  },

  exportMaps (type) {
    const mapsData = []
    for (const map of this.mapsIterator(type)) {
      const mapData = {
        x: map.column,
        y: map.line,
        tiles: [],
        characters: [],
        extras: []
      }
      for (const character of map.characters) {
        mapData.characters.push({
          x: character.column,
          y: character.line,
          sprite: character.sprite.name,
          text: character.text
        })
      }
      for (const extra of map.extras) {
        mapData.extras.push({
          x: extra.column,
          y: extra.line,
          sprite: extra.sprite.name
        })
      }
      for (const tile of map.tilesIterator()) {
        const tileData = {
          x: tile.column,
          y: tile.line,
          hitbox: tile.hitbox,
          sprite: tile.sprite.name
        }
        if (!tile.tileTransition.isEmpty()) {
          tileData.transition = {
            start: tile.tileTransition.start,
            targetMapType: tile.tileTransition.targetMapType,
            targetMapColumn: tile.tileTransition.targetMapColumn,
            targetMapLine: tile.tileTransition.targetMapLine,
            targetTileColumn: tile.tileTransition.targetTileColumn,
            targetTileLine: tile.tileTransition.targetTileLine,
            end: tile.tileTransition.end
          }
        }
        if (tile.start) {
          tileData.start = true
        }
        mapData.tiles.push(tileData)
      }
      mapsData.push(mapData)
    }
    return mapsData
  }
}
