'use strict'

import { SPRITE_TEXT_COLOR_WHITE, TILE_HEIGHT, TILE_WIDTH } from '../constant'
import textManager from '../service/text-manager'

class Character {
  constructor (column, line, x, y, width, height, sprite, text) {
    this.column = column
    this.line = line
    this.x = x
    this.y = y
    this.width = width
    this.height = height
    this.sprite = sprite
    this.text = text
    this.displayedText = ''
  }

  init () {
    this.sprite.start()
  }

  reset () {
    this.sprite.stop()
    this.displayedText = ''
  }

  hasStoppedTalking() {
    return this.displayedText === this.text
  }

  draw (context) {
    this.sprite.draw(context, this.x, this.y, this.width, this.height)

    textManager.draw(context, this.text, SPRITE_TEXT_COLOR_WHITE, this.x - 4.5 * TILE_WIDTH, this.y - 1.5 * TILE_HEIGHT, 10 * TILE_WIDTH, 1.5 * TILE_HEIGHT, this.displayedText.length)
  }
}

export { Character }
