'use strict'

class Extra {
  constructor (column, line, x, y, width, height, sprite) {
    this.column = column
    this.line = line
    this.x = x
    this.y = y
    this.width = width
    this.height = height
    this.sprite = sprite
  }

  init () {
    this.sprite.start()
  }

  reset () {
    this.sprite.stop()
  }

  draw (context) {
    this.sprite.draw(context, this.x, this.y, this.width, this.height)
  }
}

export { Extra }
